header{
   display: flex;
   justify-content: space-around;
}
header button{
    background-color: black;
    color: white;
    margin: 10px;
    padding: 10px;
    border-radius: 2rem;
   
}
header button:hover{
    background-color: beige;
    color: black;
    transition: 0.5s all;
}
